.create {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 99999;
  
    background-color: rgba(0, 0, 0, 0.5);
     
  }
  
  .create_form {
    padding: 35px;
    width: 880px;
    height: 500px;
    border-radius: 16px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    overflow: hidden;
  
    display: flex;
  
    background-color: white;
 
  }
  
  .side {
    padding: 20px;
    width: 50%;
    height: 100%;
  }
  
 
  .upload_icon_container {
    width: 32px;
    height: 32px;
    border-radius: 50%;
  
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
  
    background-color: #f0f0f0;
    cursor: pointer;
  }
  
  .upload_icon {
    width: 50%;
  }
  
  .section1 {
    width: 100%;
    height: 8%;
  
    /* background-color: purple; */
  }
  
  .section2 {
    padding: 0px 10px;
    width: 100%;
    height: 80%;
  
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  
    /* background-color: aquamarine; */
  }
  
  .section3 {
    width: 100%;
    height: 12%;
  
    display: flex;
    justify-content: center;
    align-items: center;
  
    /* background-color: firebrick; */
  }
  
 
 
  #left_side .section2 #upload_img_label {
    width: 100%;
    height: 100%;
    cursor: pointer;
  }
  
  #left_side .section2 #upload_img_label input {
    width: 0;
    height: 0;
    opacity: 0;
  }
  
  #left_side .section2 #upload_img_label .upload_img_container {
    padding: 10px;
    width: 100%;
    height: 100%;
    border-radius: 8px;
  
    background-color: #efefef;
  }
  
  #left_side .section2 #upload_img_label .upload_img_container #dotted_border {
    width: 100%;
    height: 100%;
    border-radius: 8px;
    border: 2px dashed #dadada;
  
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  
  #left_side .section2 .upload_img_container {
    background-color: #767676;
  }
  
  .img_preview_container {
    width: 252px;
    height: 512px;
    border-radius: 16px;
    position: relative;
    overflow: hidden;
    display: none;
  }
  
  .preview_img {
    width: 100%;
    height: 100%;
    position:relative;
    display: flex;
    justify-content: center;
    align-items: center;
    transition-duration: 0.5s;
  }
  .change_img{
    opacity:0;
  }
  .preview_img:hover .change_img:hover{
    position: absolute;
    bottom:0;
    background:rgba(255, 255, 255, 0.384);
    width:100%;
    height:15%;
    text-align: center;
    font-size:20px;
    font-weight: bold;
    color:white;
    opacity: 1;
    cursor: pointer;
    
  }
  
  
  
  
 
  #right_side .section1 {
      display: flex;
      justify-content: flex-end;
  }
  
  #right_side .section1 .select_size {
      width: 236px;
      height: 40px;
  
      display: flex;
  }
  
  #right_side .section1 .select_size select {
      width: 70%;
      height: 100%;
      border: none;
      border-top-left-radius: 8px;
      border-bottom-left-radius: 8px;
      outline: none;
  
      font-size: 16px;
  
      background-color: #EFEFEF;
  }
  
  #right_side .section1 .select_size .save_pin {
      width: 30%;
      height: 100%;
      border-top-right-radius: 8px;
      border-bottom-right-radius: 8px;
  
      display: flex;
      justify-content: center;
      align-items: center;
  
      font-size: 16px;
      font-weight: 700;
      
      color: white;
      background-color: #E60023;
      cursor: pointer;
  }
  
  #right_side .section2 .new_pin_input {
      margin-bottom: 20px;
      width: 100%;
      height: 50px;
      border: none;
      border-bottom: 2px solid #C6C6C6;
      outline: none;
  
      font-size: 16px;
  
      background-color: transparent;
  }